<template>
<div class="fans-container">
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <!--  -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
      <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
        <el-tab-pane label="" name="first">
          <el-row :gutter="10">
            <el-col :lg="3" :md="5" :sm="6" :xs="8" v-for="(item, index) in fansList" :key="index">
              <div class="list">
                <img :src="item.photo" alt="">
                <p>{{ item.name }}</p>
              </div>
            </el-col>
          </el-row>
          <!--  -->
          <el-pagination
            style="margin:20px 0 0 -10px"
            background
            layout="prev, pager, next"
            :disabled="getDisabled"
            :page-size="pageSize"
            @current-change="loadFansList"
            :total="totalCount">
          </el-pagination>
        </el-tab-pane>
        <el-tab-pane label="" name="second">
          <!-- 1. ECharts  DOM -->
          <div ref="main" style="width: 600px;height:400px;"></div>
        </el-tab-pane>
      </el-tabs>
  </el-card>
</div>
</template>

<script>
import { getFans } from '@/api/user'
import echarts from 'echarts'
export default {
  name: 'FansIndex',
  components: {},
  props: {},
  data () {
    return {
      activeName: 'first',
      fansList: [],
      getDisabled: false, // 
      pageSize: 20, // 
      totalCount: 0 // 
    }
  },
  computed: {},
  watch: {},
  created () {
    this.loadFansList()
  },
  mounted () {
    // 2.domecharts
    var myChart = echarts.init(this.$refs.main)
    // 3.
    var option = {
      title: {
        text: 'ECharts '
      },
      tooltip: {},
      legend: {
        data: ['']
      },
      xAxis: {
        data: ['', '', '', '', '', '']
      },
      yAxis: {},
      series: [{
        name: '',
        type: 'bar',
        data: [5, 20, 36, 10, 10, 20]
      }]
    }
    // 4.
    myChart.setOption(option)
  },
  methods: {
    handleClick (tab, event) {
      console.log(tab, event)
    },
    // 
    loadFansList (page = 1) {
      this.getDisabled = true
      getFans({
        page,
        per_page: this.pageSize
      }).then(res => {
        console.log(res)
        this.fansList = res.data.data.results
        this.totalCount = res.data.data.total_count

        // 
        this.getDisabled = false
      })
    }
    //  loadFansList
    // onCurrentChange (page) {
    //   this.loadFansList(page)
    // }
  }
}
</script>

