<template>
<div class="upload-cover">
  <div class="cover-image" @click="isShowDialog">
      <img :src="value" alt="" class="coverImage" ref="upload-image">
  </div>
  <el-dialog
    title=""
    :visible.sync="dialogVisible"
    append-to-body
  >
    <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
      <el-tab-pane label="" name="first">
        <cover-image ref="cover-image" style="width:100%" :is-show-add='false' :is-show-action='false' is-show-select></cover-image>
      </el-tab-pane>
      <el-tab-pane label="" name="second" class="preview-image-wrap">
        <!-- inputchange -->
        <input type="file" ref="file" @change="onChangeImage">
        <!--  -->
        <img :src="previewImage" class="preview-image">
      </el-tab-pane>
    </el-tabs>
    <span slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false"> </el-button>
      <el-button type="primary" @click="onUploadImage"> </el-button>
    </span>
  </el-dialog>
</div>
</template>

<script>
import { uploadImages } from '@/api/image'
import coverImage from '@/views/image/components/cover-image'
export default {
  name: 'UploadCover',
  components: {
    coverImage
  },
  props: ['value'],
  data () {
    return {
      dialogVisible: false, // ,
      activeName: 'first',
      previewImage: '' // 
    }
  },
  computed: {},
  watch: {},
  created () {},
  mounted () {},
  methods: {
    isShowDialog () {
      this.dialogVisible = true
    },
    handleClick (tab, event) {
      console.log(tab, event)
    },
    // input
    onChangeImage () {
      // fileDOM
      const file = this.$refs.file
      // 
      const fileObj = file.files[0]
      // blob url
      const blob = window.URL.createObjectURL(fileObj)
      // 
      this.previewImage = blob
      // 
    //   this.$refs.file.value = ''
    },
    // 
    onUploadImage () {
      // ,
      if (this.activeName === 'second') {
        const file = this.$refs.file.files[0]
        if (!file) {
          this.$message('')
          return
        }
        const fd = new FormData()
        fd.append('image', file)
        uploadImages(fd).then(res => {
          // 
          this.dialogVisible = false
          // 
          this.$refs['upload-image'].src = res.data.data.url

          // 
          this.$emit('input', res.data.data.url)
        })
      } else if (this.activeName === 'first') {
        // 
        // , 
        const coverImage = this.$refs['cover-image']
        console.log(coverImage)
        // 
        const select = coverImage.select
        if (select === null) {
          this.$message('')
          return
        }
        // 
        this.dialogVisible = false
        // 
        this.$emit('input', coverImage.image[select].url)
      }
    }
  }
}
</script>

<style scoped lang="less">
.cover-image {
    width: 120px;
    height: 150px;
    border: 1px solid #ccc;
    .coverImage {
        max-width: 100%;
        height: 150px;
    }
}
.preview-image-wrap {
    display: flex;
    flex-direction: column;
    .preview-image {
        width: 100px;
        height: 150px;
        margin-top: 10px;
    }
}
</style>
