<template>
<div class="cover-image">
    <div style="margin-bottom: 20px">
      <el-radio-group v-model="collect" size="mini" @change="onCollectImages(1)">
        <el-radio-button :label="false"></el-radio-button>
        <el-radio-button :label="true"></el-radio-button>
      </el-radio-group>
      <el-button type="success" @click="dialogUploadVisible = true" size="mini" style="float: right" v-if="isShowAdd"></el-button>
    </div>
    <el-row :gutter="10">
      <el-col :xs="12" :sm="6" :md="6" :lg="4" v-for="(img,index) in image" :key="index" >
        <div class="img-list">
          <el-image
            style="height: 100px"
            :src="img.url"
            fit="fit"
            @click.native="select = index"
          >
          </el-image>
          <div class="mask" v-if="isShowAction">
            <!-- ,loading -->
            <!--  -->
            <el-button type="warning" :class="{'el-icon-star-on': img.is_collected, 'el-icon-star-off': !img.is_collected}" circle size="mini" @click="onChangeCollected(img)" :loading="img.loading"></el-button>
            <!--  -->
            <el-button type="danger" icon="el-icon-delete" circle size="mini" @click="onDeleteImage(img)" :loading="img.loading"></el-button>
          </div>
          <div class="select" v-if="isShowSelect && select === index"></div>
        </div>
      </el-col>
    </el-row>
    <!--  -->
    <el-pagination
      style="margin-top:20px"
      :page-size="pageSize"
      :current-page="currentPage"
      @current-change="loadImages"
      background
      layout="prev, pager, next"
      :total="totalCount">
    </el-pagination>
  <!--  -->
  <!-- :modal-append-to-body='false' ,,append-to-body -->
  <el-dialog title=""
   :visible.sync="dialogUploadVisible"
   :append-to-body="true"
   width="26.5%"
  >
    <!-- upload 
       POST
       action 
       headers
       name
    -->
    <el-upload
      class="upload-demo"
      drag
      name="image"
      action="http://ttapi.research.itcast.cn/mp/v1_0/user/images"
      :headers="UploadHeaders"
      :show-file-list="true"
      :on-success="uploadSuccessImage"
      multiple>
      <i class="el-icon-upload"></i>
      <div class="el-upload__text"><em></em></div>
      <div class="el-upload__tip" slot="tip">jpg/png500kb</div>
    </el-upload>
  </el-dialog>
</div>
</template>

<script>
import { getImages, collectImage, deleteImage } from '@/api/image'
export default {
  name: 'CoverImage',
  components: {},
  props: {
    isShowAdd: {
      type: Boolean,
      default: true
    },
    isShowAction: {
      type: Boolean,
      default: true
    },
    isShowSelect: {
      type: Boolean,
      default: false
    }
  },
  data () {
    const user = JSON.parse(window.localStorage.getItem('user'))
    return {
      collect: false,
      image: [], // 
      currentPage: 1, // ,
      pageSize: 10, // 
      totalCount: 0, // 
      dialogUploadVisible: false, // 
      UploadHeaders: { // 
        Authorization: `Bearer ${user.token}`
      },
      select: null // 
    }
  },
  computed: {},
  watch: {},
  created () {
    this.loadImages(1)
  },
  mounted () {},
  methods: {
    loadImages (page = 1) {
      // ()
      // ,
      this.currentPage = page
      getImages({
        collect: this.collect,
        page,
        per_page: this.pageSize
      }).then(res => {
        // loadingimg,,,
        const results = res.data.data.results
        results.forEach(img => {
          img.loading = false
        })
        this.image = results
        this.totalCount = res.data.data.total_count
      })
    },
    // 
    onCollectImages (value) {
      this.loadImages(value)
    },
    // 
    uploadSuccessImage () {
      // 
      this.dialogUploadVisible = false
      // 
      this.loadImages(this.page)
      this.$message({
        type: 'success',
        message: ''
      })
    },
    // 
    onChangeCollected (img) {
      img.loading = true
      collectImage(img.id, !img.is_collected).then(res => {
        img.is_collected = !img.is_collected
        if (img.is_collected) {
          this.loadImages()
          this.$message({
            type: 'success',
            message: ''
          })
        } else {
          this.loadImages()
          this.$message({
            type: 'success',
            message: ''
          })
        }
        img.loading = false
      })
    },
    // 
    onDeleteImage (img) {
      img.loading = true
      deleteImage(img.id).then(res => {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.loadImages(this.currentPage)
          this.$message({
            type: 'success',
            message: '!'
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
        img.loading = false
      })
    }
  }
}
</script>

<style scoped lang="less">
.img-list {
  position: relative;
}
.mask {
  position: absolute;
  bottom: 5px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  height: 25px;
  background: rgba(1, 1, 1, .3);
  z-index: 10000;
}
.select {
    background: url(./selected.png) no-repeat;
    background-size: cover;
    max-width: 100%;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}
</style>
