<template>
<div class="publish-container">
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <!--  -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item>{{ $route.query.id ? '' : '' }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <!--  -->
    <el-form ref="form" :model="article" label-width="60px" :rules="formRules">
      <el-form-item label="" prop="title">
        <el-input v-model="article.title"></el-input>
      </el-form-item>
      <el-form-item label="" prop="content">
        <!-- <el-input type="textarea" v-model="article.content"></el-input> -->
        <el-tiptap
          v-model="article.content"
          :extensions="extensions"
          placeholder=""
        />
      </el-form-item>
      <el-form-item label="" prop="cover">
        <el-radio-group v-model="article.cover.type">
          <el-radio :label="1"></el-radio>
          <el-radio :label="3"></el-radio>
          <el-radio :label="0"></el-radio>
          <el-radio :label="-1"></el-radio>
        </el-radio-group>
        <!-- template -->
        <template v-if="article.cover.type > 0">
          <upload-cover v-for="(cover, index) in article.cover.type" :key="index" v-model="article.cover.images[index]"></upload-cover>
        </template>
      </el-form-item>
      <el-form-item label="" prop="channel_id">
        <el-select v-model="article.channel_id" placeholder="">
          <el-option v-for="(channel,index) in channels" :key="index" :label="channel.name" :value="channel.id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onPublish(false)"></el-button>
        <el-button @click="onPublish(true)"></el-button>
      </el-form-item>
    </el-form>
    <!-- / -->
  </el-card>
</div>
</template>

<script>
import { getArticleChannels, publishArticle, editArticl, getEditArticle } from '@/api/article'
import { uploadImages } from '@/api/image'
// import this package's styles
import 'element-tiptap/lib/index.css'
import {
  // necessary extensions 
  ElementTiptap,
  Doc,
  Text,
  Paragraph, // 
  Heading, // 
  Bold, // 
  Underline, // 
  Italic, // 
  Strike, // 
  Image,
  ListItem,
  BulletList, // 
  OrderedList, // 
  CodeBlock,
  Fullscreen, // 
  Preview
} from 'element-tiptap'
// 
import UploadCover from './components/upload-cover'
export default {
  name: 'PublishIndex',
  components: {
    // 
    'el-tiptap': ElementTiptap,
    UploadCover
  },
  props: {},
  data () {
    return {
      article: {
        title: '',
        content: '',
        cover: {
          type: 1,
          images: []
        },
        channel_id: null
      },
      channels: [], // 
      // editor extensions
      // they will be added to menubar and bubble menu by the order you declare.
      extensions: [
        new Doc(),
        new Text(),
        new Paragraph(),
        new Heading({ level: 5 }), // 5
        new Bold({ bubble: true }), // render command-button in bubble menu. 
        new Underline(),
        new Italic(),
        new Strike(), // 
        new Image({
          // 
          uploadRequest (file) {
            const fd = new FormData()
            fd.append('image', file)
            // 
            return uploadImages(fd).then(res => {
              return res.data.data.url
            })
          }
        }), // ,
        new ListItem(),
        new BulletList(),
        new OrderedList(),
        new CodeBlock(), // ,
        new Fullscreen(),
        new Preview() // 
      ],
      // 
      formRules: {
        title: [
          { required: true, message: '', tigger: 'blur' },
          { min: 5, max: 30, message: '530', tigger: 'blur' }
        ],
        content: [
          {
            // 
            validator (rule, valid, callback) {
              if (valid === '<p></p>') {
                callback(new Error(''))
              }
              callback()
            }
          },
          { required: true, message: '', tigger: 'blur' }
        ],
        channel_id: [
          { required: true, message: '' }
        ]
      }
    }
  },
  methods: {
    // 
    loadChannels () {
      getArticleChannels().then(res => {
        this.channels = res.data.data.channels
      })
    },
    // 
    onPublish (draft = false) {
      // 
      this.$refs.form.validate(value => {
        if (!value) {
          // 
          return
        }
        // 
        const articleId = this.$route.query.id
        if (articleId) {
          // id
          editArticl(articleId, this.article, draft).then(res => {
            this.$message({
              type: 'success',
              message: ''
            })
            this.$router.push('/article')
          })
        } else {
          //  ,
          publishArticle(this.article, draft).then(res => {
            console.log(res)
            this.$message({
              type: 'success',
              message: `${draft ? '' : ''}`
            })
            this.$router.push('/article')
          })
        }
      })
    },
    // ,
    getAticle () {
      getEditArticle(this.$route.query.id).then(res => {
        this.article = res.data.data
      })
    }
  },
  computed: {},
  watch: {},
  created () {
    this.loadChannels()

    // 
    if (this.$route.query.id) {
      this.getAticle()
    }
  },
  mounted () {}
}
</script>

<style scoped lang="less"></style>
